import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import './App.css';
import CustomNavbar from './components/navbar/Navbar';
import Home from './components/home/Home';
import AdventureFarming from './components/adventure/AdventureFarming';
import WarPatterns from './components/warpattern/WarPatterns';
import Partners from './components/partners_and_battling/PartnersAndBattling';
import About from './components/about/About';

class App extends Component {
  render() {
    return (  
      <Router basename="baguide">
        <React.Fragment>
          <CustomNavbar />
          <Route exact path="/:subdir" component={Navigator} />
        </React.Fragment>
      </Router>
    );
  }
}

export default App;

// a component to help overcome github problems to access sub directory directly 
class Navigator extends Component {
  render(){
    let subdir = this.props.match.params.subdir;
    console.log(subdir);
    switch(subdir){
      case "adventure":
        return <AdventureFarming />
      case "partners":
        return <Partners />
      case "patterns":
        return <WarPatterns />
      case "about":
        return <About />
      default: 
        return <Home />
    }
  }
}


